.section-box {
  max-width: 900px;
  width: 100%;
  text-align: center;
  margin: 72px auto;
  /* padding: 0px 32px; */
}
.section-box-100 {
  height: 100%;
  color: #fff;
  margin: 0px;
  padding: 10px;
  line-height: 1px;
}
.section-box-50 {
  height: 80%;
  background-color: red;
}
.cards-btn {
  text-align: center!important;
  color: #fff !important;
  background-color: #ff4d4f !important;
  border-color: #ff4d4f !important;
  font-size: 1em!important;
  text-transform: uppercase!important;
  font-weight: 600!important;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.5s linear;
}
.cards:hover .cards-btn {
  visibility: visible;
  opacity: 1;
}
.cards{
  border-radius: 2px!important;
}
.cards .ant-card-body {
  top: 0px;
  left: 0px;
  position: absolute;
  background-color: #00000054;
  height: 100%;
  width: 100%;
  color: white;
  /* display: none; */
  align-items: center;
  display: flex;
  justify-content: center;
  border-radius: 4px;
}
.cards .cards-content {
  color: white !important;
}

.cards-description{
  text-align: center;
}