body{
	background: #fdfdfd!important
}

.padtop15{padding-top: 15px}
.padtop20{padding-top: 20px!important}
.padtop30{padding-top: 30px!important}
.header_home .menu_nav ul li a{color:#888;}
.header_home .menu_nav ul li a:hover{color:#fff;}

.espec_hid{
	position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    background: none!important;
}

.contact-page-form{
	background: #fdfdfd;
	text-align: center;
    width: 730px;
    margin: auto;
    padding-top: 30px
}

.contact-page-form h2,.contact-page-form h4, .contact-page-form h6{
	color:#777;
}

.contact-page-form .input-group input[type=text]{
	border-radius: 0px!important;
	padding:6px 10px;
	height: 45px;
	box-shadow: none;
}

.contact-page-form .input-group textarea{
	border-radius: 0px!important;
	padding:6px 10px;
	height: 90px;
	box-shadow: none;
}


.contact-page-form::-webkit-input-placeholder {
  color: #afafaf;
  font-size: 16px!important;
}

/* line 2, ../scss/helpers/_forms.scss */
.contact-page-form::-moz-placeholder {
  color: #afafaf;
  font-size: 16px!important;
}

/* line 3, ../scss/helpers/_forms.scss */
.contact-page-form:-moz-placeholder {
  color: #afafaf;
  font-size: 16px!important;
}

/* line 4, ../scss/helpers/_forms.scss */
.contact-page-form:-ms-input-placeholder {
  color: #afafaf;
  font-size: 16px!important;
}

@media(max-width: 768px){
  .contact-page-form{width: 100%}
}