#header header {
    padding: 10px 50px;
    box-sizing: border-box;
    position:absolute;
    z-index: 100;
    background: none

}

header .menu_bar {
    display: inline-block;
    max-width: 200px;
    vertical-align: middle;
    z-index: 1000
}

header .ctr_menu {
    display: none
}

header nav {
    z-index: 1000;
    max-width: 1000px;
    text-align: right;
    float: right
}

header nav ul {
    list-style: none;
    margin: 0!important;
    padding-left: 0px
}

header nav ul li {
    display: inline-block;
    position: relative
}

header nav ul li{
    color: #fff
}

header nav ul li a:hover {
    background: #444;
    color: #fff
}

header nav ul li a {
    font: 700 14px 'Open Sans';
    color: #fff;
    display: block;
    text-decoration: none!important;
    padding: 10px
}

