    text-align: center
}

header nav ul li a:hover {
    color: #fff
}

header nav ul li a span {
    margin-right: 10px
}

header nav ul li:hover .children {
    display: block;
}

header nav ul li .children {
    display: none;
    background: #f9f9f9;
    position: absolute;
    width: 200%;
    z-index: 1000
}

header nav ul li .caret,
header nav ul li .children li a span {
    position: relative;
    top: 3px;
    margin-left: 10px;
    margin-right: 0
