body {
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
}
.mobile-wrapper {
  background-color: #4d3995 !important;
  position: absolute;
  overflow-x: hidden;
  height: 100%;
  width: 100%;
}
